import React from 'react';
import PropTypes from 'prop-types';
import { StyledDiagram } from './Diagram.style';
import { AreaChart, Area, YAxis, Tooltip, ResponsiveContainer } from 'recharts';

const Diagram = (props) => {
  const data = [
    {
      uv: 40,
    },
    {
      uv: 45,
    },
    {
      uv: 47,
    },
  ];
  return (
    <StyledDiagram>
      <ResponsiveContainer>
        <AreaChart
          data={data}
          margin={{
            top: 60,
            right: 0,
            left: 0,
            bottom: 0,
          }}
        >
          <defs>
            <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
              <stop offset="5%" stopColor="#EB5757" stopOpacity={0.6} />
              <stop offset="100%" stopColor="#EB5757" stopOpacity={0} />
            </linearGradient>
          </defs>
          <Tooltip />
          <YAxis type="number" hide={true} domain={[37, 47]} />
          <Area
            type="monotone"
            dataKey="uv"
            stroke="#C4686F"
            fillOpacity={1}
            fill="url(#colorUv)"
          />
        </AreaChart>
      </ResponsiveContainer>
    </StyledDiagram>
  );
};

Diagram.propTypes = {};

export default Diagram;
